@import "/src/common/styles/sass/variables";

.title {
  margin: 0 0 40px;
  font-family: 'Shippori Mincho', serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 5px;
  position: relative;

  &::after {
    content: "";
    background-color: $textColor;
    height: 1px;
    width: 55px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 50%);
    bottom: -30px;
  }

  h2 {
    margin: 0;
  }
}




